export default {
	data() {
		return {
			sum: 0
		};
	},
	methods: {
		calmax: function(e) {
			let sum = 0;
			let max = parseInt(e.detail.value['num']);
			console.log(max);
			for (var i = 1; i < max; i++) {
				if (i % 2 == 0) {
					sum = sum + i;
				}
			}
			this.sum = sum;
			console.log(sum);
		}
	}
};
